
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import ecc from 'eosjs-ecc'
import Divider from "../node_modules/vux/src/components/divider/index";
import { eosApi } from './api'
import Clipboard from 'clipboard'

export default {
components: {
Divider,
Group,
Cell
},
data () {
return {
nameError: '',
keyError: 'KEY',
key: '',
name: '',
account: null,
currentEOS: null
}
},
created() {
this.getPrice();
},
methods: {
checkPublicKey (value) {
if (ecc.isValidPublic(value)) {
this.keyError = null
} else {
this.keyError = ''
}
},
checkName (value) {
this.account = null;
if (value.length == 0) {
this.nameError = '';
} else if (value.length !== 12){
this.nameError = ' 12 ';
} else if (/[a-z1-5]{12}/.test(value)){
eosApi.getAccount(value).then(function (account) {
this.account = account;
this.nameError = '';
}.bind(this), function (err) {
this.nameError = null;
}.bind(this))
} else {
this.nameError = '';
}
},
copy () {
this.$copyText(this.name+'-'+this.key).then(function (e) {
alert('')
}, function (e) {
alert('')
})
},
getPrice() {
eosApi.getTableRows(true, 'eosio', 'eosio', 'rammarket').then(function (data) {
var row = data.rows[0];
var eos = parseFloat(row.quote.balance.split(' ')[0]);
var ram = parseInt(row.base.balance.split(' ')[0]);
var currentEOS = (eos * 4096)/(ram-4096) + 0.2;
this.currentEOS = Math.floor(currentEOS*100, 2) / 100;

}.bind(this))
}
},
}



