
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { eosApi } from './api'

export default {
components: {
Group,
Cell
},
data () {
return {
msg: 'Hello World!',
tx_id: '',
name: null,
}
},
methods: {
getTransaction () {
this.name = null;
var tx_id = this.tx_id.trim();
if (tx_id.length != 64) {
alert(' ID  64 ');
return
}

eosApi.getTransaction(this.tx_id).then(function (data) {
var action = data.trx.trx.actions[0];
if (action.account != 'eosio.token' || action.name != 'transfer') {
this.name = null;
alert(' EOS ');
} else {
if (action.data.to != 'signupeoseos') {
this.name = null;
alert(' signupeoseos');
} else {
this.name = action.data.memo.split('-')[0];
}
}
}.bind(this));
}
},
}



